html,
* {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.panel {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: calc(50% - 340px);
  left: 30px;
  width: 400px;
  height: 680px;
  background: #ffffff;
  box-shadow: 4px 4px 10px 10px rgba(0, 0, 0, 0.1);
  padding: 50px;
  border-radius: 20px;
  overflow: hidden;
}

.panel * {
  width: 100%;
  text-decoration: none;
}

.panel .content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 50px;
}

.panel .copyright-wrapper {
  width: 150px;
  height: 150px;
  margin: 0 auto;
  text-align: left;
  font-size: 14px;
  line-height: 19px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  justify-content: flex-start;
  align-items: center;
  border-radius: 50%;
  bottom: -78px;
  background-color: var(--primary-color);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: 1px solid transparent;
}

.panel .copyright-wrapper span,
.panel .copyright-wrapper a {
  color: white;
  text-align: center;
  display: inline-block;
  line-height: 25px;
  font-size: 11px;
}

.panel .copyright-wrapper a {
  text-decoration: underline;
}

.panel .copyright-wrapper:hover {
  background: white;
  border-color: var(--primary-color);
}

.panel .copyright-wrapper:hover span,
.panel .copyright-wrapper:hover a {
  color: var(--primary-color);
}

.panel .back-icon {
  position: absolute;
  left: 48px;
  top: 36px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
}

.panel .back-icon:hover {
  color: var(--primary-color);
}

.logo {
  width: 100%;
  height: auto;
  text-align: center;
}

.logo .logo-title {
  font-size: 22px;
  font-weight: bolder;
  color: black;
  margin: 0 auto;
  text-transform: uppercase;
}

.logo .logo-description {
  font-size: 15px;
  font-weight: 600;
  color: black;
  margin: 0 auto;
  display: inline-block;
  margin: 0 auto 20px auto;
  text-transform: uppercase;
}

.logo img {
  width: 100px;
  height: auto;
  background-size: cover;
}

.d-flex {
  display: flex;
  padding: 0;
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

.gap-15 {
  grid-gap: 15px;
  gap: 15px;
}

.w-50 {
  width: 50%;
}

@media (max-width: 540px) {
  .panel {
    width: 95%;
    height: 95%;
    position: relative;
    margin: auto;
    top: 0;
    left: 0;
    padding: 22px;
  }
  .panel .back-icon {
    left: 18px;
    top: 18px;
  }
  .panel .logo img {
    width: 80px;
  }
  .panel .logo .logo-title {
    font-size: 18px;
    font-weight: bolder;
    color: black;
    margin: 0 auto;
    text-transform: uppercase;
  }
  .panel .logo .logo-description {
    font-size: 13px;
    font-weight: 600;
    color: black;
    margin: 0 auto;
    display: inline-block;
    margin: 0 auto 10px auto;
    text-transform: uppercase;
  }
  .panel .copyright-wrapper {
    width: 130px;
    height: 130px;
    margin: 0 auto;
    text-align: left;
    font-size: 12px;
    line-height: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    justify-content: flex-start;
    align-items: center;
    border-radius: 50%;
    bottom: -70px;
  }
}

.privacy-link {
  text-decoration: underline;
}
